import { apiLogin, apiLogout } from '@/api/user/login'
import { apiGetUserInfo } from '@/api/user/profile'
import { getToken, setToken, removeToken } from '@/utils/auth'
import { baseURL } from '@/utils/config'
import storage from '@/utils/cache'
let { local: localCache } = storage

const persistAvatar = localCache.get('AVATAR') || require('@/assets/images/default-avatar.jpg')
const persistUserName = localCache.get('USERNAME') || '神秘人'
const persistUserId = localCache.get('USERID') || ''

const user = {
  state: {
    token: getToken(),
    name: persistUserName,
    avatar: persistAvatar,
    userId: persistUserId,
    myGoodsNum: 0,
    mySubjectNum: 0,
    myBundleNum: 0,
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NAME: (state, name) => {
      state.name = name
      localCache.set('USERNAME', name)
    },
    SET_USERID: (state, userId) => {
      state.userId = userId
      localCache.set('USERID', userId)
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
      localCache.set('AVATAR', avatar)
    },
    SET_MYGOODSNUM: (state, num) => {
      state.myGoodsNum = num
    },
    SET_MYSUBJECTNUM: (state, num) => {
      state.mySubjectNum = num
    },
    SET_MYBUNDLENUM: (state, num) => {
      state.myBundleNum = num
    },
  },

  actions: {
    // 登录
    Login({ commit, dispatch }, data) {
      let { type, params } = data
      return new Promise((resolve, reject) => {
        apiLogin(type, params)
          .then((res) => {
            let { access_token, expires_in } = res.data
            setToken(access_token)
            commit('SET_TOKEN', access_token)
            dispatch('GetUserInfo').then(() => dispatch('GetShoppingCart').then(() => resolve()))
          })
          .catch((error) => {
            reject(error)
          })
      })
    },
    //app登录
    appLogin({ commit, dispatch },data) {
      return new Promise((resolve, reject) => {
      let access_token  =  data
      setToken(access_token)
      commit('SET_TOKEN', access_token)
      dispatch('GetUserInfo').then(() => dispatch('GetShoppingCart').then(() => resolve())).catch((error) => {
        reject(error)
        })
      })
    },
    // 获取用户信息
    GetUserInfo({ commit, state }) {
      return new Promise((resolve, reject) => {
        apiGetUserInfo()
          .then((res) => {
            const userInfo = res.data || {}
            let nickName = userInfo.truename || '神秘人'
            let avatar = userInfo.avatar || require('@/assets/images/default-avatar.jpg')
            let userId = userInfo.frontUserId || ''
            commit('SET_NAME', nickName)
            commit('SET_AVATAR', avatar)
            commit('SET_USERID', userId)
            resolve(userInfo)
          })
          .catch((error) => {
            reject(error)
          })
      })
    },

    // 退出系统
    LogOut({ commit }) {
      return new Promise((resolve, reject) => {
        apiLogout()
          .then(() => {
            commit('SET_TOKEN', '')
            commit('SET_NAME', '')
            commit('SET_AVATAR', '')
            commit('SET_USERID', '')
            commit('setShoppingCart', [])
            commit('SET_MYGOODSNUM', 0)
            commit('SET_MYSUBJECTNUM', 0)
            commit('SET_MYBUNDLENUM', 0)
            removeToken()
            resolve()
          })
          .catch((error) => {
            reject(error)
          })
      })
    },

    // 前端 登出
    FedLogOut({ commit }) {
      return new Promise((resolve) => {
        commit('SET_TOKEN', '')
        commit('SET_NAME', '')
        commit('SET_AVATAR', '')
        commit('SET_USERID', '')
        commit('setShoppingCart', [])
        commit('SET_MYGOODSNUM', 0)
        commit('SET_MYSUBJECTNUM', 0)
        commit('SET_MYBUNDLENUM', 0)
        removeToken()
        resolve()
      })
    },
  },
}

export default user
