<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { apiGetCartList } from "@/api/cart/cart";
export default {
  name: "App",
  computed: {
    ...mapGetters(["token", "userId"]),
  },
  created() {
    if (!this.token) {
      this.$store.dispatch("setShoppingCart", []);
    } else {
      apiGetCartList({ userId: this.userId })
        .then((res) => {
          let { rows = [] } = res;
          let data = [];
          rows.map((item) => {
            data.push({ ...item, isCheck: false });
          });
          this.$store.dispatch("setShoppingCart", data);
        })
        .catch((err) => this.$store.dispatch("setShoppingCart", []));
    }
  },
};
</script>

